import db from '../models';
import { success, failure } from '../lib/http-responses';

export const handler = async (event) => {
  try {
    const { Order } = await db();
    const orders = await Order.find({});
    return success(
      {
        status: 'ok',
        message: 'Orders',
        data: orders,
      },
      event.requestContext
    );
  } catch (error) {
    console.log(error);
    return failure({
      success: false,
      message: error.message || 'There was a problem fetching the orders.',
    });
  }
};
